"use client";

import { useState, useEffect } from "react";
import { BarChart2, ShoppingBag, Users, DollarSign, TrendingUp, Package, Star } from "lucide-react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import { Montserrat } from "next/font/google";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["400", "800"],
});

interface Stats {
  totalSales: number;
  totalProducts: number;
  totalItems: number;
  totalUsers: { clients: number; sellers: number };
  topSellingProduct: { name: string; sold: number };
  mostProfitableProduct: { name: string; profit: number };
  soldProducts: { products: number; items: number };
  productRatings: {
    highest: { name: string; rating: number; reviews: number };
    lowest: { name: string; rating: number; reviews: number };
  };
}

export default function DashboardPage() {
  const [stats] = useState<Stats>({
    totalSales: 125000,
    totalProducts: 50,
    totalItems: 320,
    totalUsers: { clients: 120, sellers: 15 },
    topSellingProduct: { name: "T-shirt Vintage", sold: 45 },
    mostProfitableProduct: { name: "Chaussures Sport", profit: 15000 },
    soldProducts: { products: 30, items: 150 },
    productRatings: {
      highest: { name: "T-shirt Vintage", rating: 4.5, reviews: 123 },
      lowest: { name: "Casquette Simple", rating: 1, reviews: 100 },
    },
  });

  const [selectedPeriod, setSelectedPeriod] = useState<"week" | "month" | "year">("week");

  const weekData = [
    { day: "Lun", sales: 5000, itemsSold: 20 },
    { day: "Mar", sales: 7000, itemsSold: 30 },
    { day: "Mer", sales: 3000, itemsSold: 15 },
    { day: "Jeu", sales: 9000, itemsSold: 40 },
    { day: "Ven", sales: 6000, itemsSold: 25 },
    { day: "Sam", sales: 12000, itemsSold: 50 },
    { day: "Dim", sales: 8000, itemsSold: 35 },
  ];

  const monthData = [
    { day: "1-5", sales: 15000, itemsSold: 60 },
    { day: "6-10", sales: 20000, itemsSold: 80 },
    { day: "11-15", sales: 12000, itemsSold: 50 },
    { day: "16-20", sales: 25000, itemsSold: 100 },
    { day: "21-25", sales: 18000, itemsSold: 70 },
    { day: "26-30", sales: 30000, itemsSold: 120 },
  ];

  const yearData = [
    { month: "Jan", sales: 40000, itemsSold: 150 },
    { month: "Fév", sales: 35000, itemsSold: 130 },
    { month: "Mar", sales: 50000, itemsSold: 200 },
    { month: "Avr", sales: 45000, itemsSold: 180 },
    { month: "Mai", sales: 60000, itemsSold: 250 },
    { month: "Juin", sales: 55000, itemsSold: 220 },
    { month: "Juil", sales: 70000, itemsSold: 300 },
    { month: "Août", sales: 65000, itemsSold: 270 },
    { month: "Sept", sales: 50000, itemsSold: 200 },
    { month: "Oct", sales: 55000, itemsSold: 220 },
    { month: "Nov", sales: 60000, itemsSold: 250 },
    { month: "Déc", sales: 75000, itemsSold: 320 },
  ];

  const chartData = selectedPeriod === "week" ? weekData : selectedPeriod === "month" ? monthData : yearData;
  const xAxisKey = selectedPeriod === "year" ? "month" : "day";
  const chartTitle =
    selectedPeriod === "week"
      ? "Évolution des ventes (7 jours)"
      : selectedPeriod === "month"
      ? "Évolution des ventes (30 jours)"
      : "Évolution des ventes (12 mois)";

  // Animation d'entrée des cartes
  useEffect(() => {
    const cards = document.querySelectorAll(".data-card");
    cards.forEach((card, index) => {
      card.animate(
        [
          { transform: "translateY(50px)", opacity: 0 },
          { transform: "translateY(0)", opacity: 1 },
        ],
        {
          duration: 600,
          delay: index * 100,
          easing: "ease-out",
          fill: "forwards",
        }
      );
    });
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        {/* Titre */}
        <div className="mb-12 flex items-center gap-4">
          <BarChart2 className="h-10 w-10 text-teal-600 animate-pulse" />
          <h1
            className={`text-5xl font-extrabold text-gray-900 tracking-tight ${montserrat.className}`}
          >
            Tableau de Bord
          </h1>
        </div>

        {/* Cartes statistiques */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 mb-12">
          <div className="data-card bg-white/70 backdrop-blur-lg border border-teal-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-teal-300/30">
            <DollarSign className="h-12 w-12 text-teal-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Ventes Totales</h3>
            <p className="text-3xl font-bold text-gray-900 mt-2">
              {stats.totalSales.toLocaleString()} DZD
            </p>
          </div>

          <div className="data-card bg-white/70 backdrop-blur-lg border border-purple-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-purple-300/30">
            <ShoppingBag className="h-12 w-12 text-purple-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Produits</h3>
            <p className="text-3xl font-bold text-gray-900 mt-2">
              {stats.totalProducts}
            </p>
            <p className="text-sm text-gray-600">{stats.totalItems} articles</p>
          </div>

          <div className="data-card bg-white/70 backdrop-blur-lg border border-indigo-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-indigo-300/30">
            <Users className="h-12 w-12 text-indigo-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Utilisateurs</h3>
            <p className="text-3xl font-bold text-gray-900 mt-2">
              {stats.totalUsers.clients + stats.totalUsers.sellers}
            </p>
            <p className="text-sm text-gray-600">
              {stats.totalUsers.clients} clients, {stats.totalUsers.sellers} vendeurs
            </p>
          </div>

          <div className="data-card bg-white/70 backdrop-blur-lg border border-orange-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-orange-300/30">
            <TrendingUp className="h-12 w-12 text-orange-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Top Vente</h3>
            <p className="text-2xl font-bold text-gray-900 mt-2">
              {stats.topSellingProduct.name}
            </p>
            <p className="text-sm text-gray-600">{stats.topSellingProduct.sold} unités</p>
          </div>

          <div className="data-card bg-white/70 backdrop-blur-lg border border-yellow-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-yellow-300/30">
            <DollarSign className="h-12 w-12 text-yellow-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Top Profit</h3>
            <p className="text-2xl font-bold text-gray-900 mt-2">
              {stats.mostProfitableProduct.name}
            </p>
            <p className="text-sm text-gray-600">
              {stats.mostProfitableProduct.profit.toLocaleString()} DZD
            </p>
          </div>

          <div className="data-card bg-white/70 backdrop-blur-lg border border-pink-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-pink-300/30">
            <Package className="h-12 w-12 text-pink-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Vendus</h3>
            <p className="text-3xl font-bold text-gray-900 mt-2">
              {stats.soldProducts.products}
            </p>
            <p className="text-sm text-gray-600">{stats.soldProducts.items} articles</p>
          </div>
        </div>

        {/* Cartes de notes */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-12">
          <div className="data-card bg-white/70 backdrop-blur-lg border border-green-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-green-300/30">
            <Star className="h-12 w-12 text-green-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Meilleure Note</h3>
            <p className="text-2xl font-bold text-gray-900 mt-2">
              {stats.productRatings.highest.name}
            </p>
            <p className="text-sm text-gray-600">
              {stats.productRatings.highest.rating}/5 ({stats.productRatings.highest.reviews} avis)
            </p>
          </div>

          <div className="data-card bg-white/70 backdrop-blur-lg border border-red-200/50 p-6 rounded-2xl shadow-xl transform transition-all duration-500 hover:-translate-y-2 hover:shadow-red-300/30">
            <Star className="h-12 w-12 text-red-500 animate-float" />
            <h3 className="text-lg font-semibold text-gray-800 mt-4">Pire Note</h3>
            <p className="text-2xl font-bold text-gray-900 mt-2">
              {stats.productRatings.lowest.name}
            </p>
            <p className="text-sm text-gray-600">
              {stats.productRatings.lowest.rating}/5 ({stats.productRatings.lowest.reviews} avis)
            </p>
          </div>
        </div>

        {/* Graphique */}
        <div className="bg-white/90 backdrop-blur-lg border border-gray-200/50 p-8 rounded-2xl shadow-xl">
          <div className="flex justify-between items-center mb-8">
            <h2 className="text-2xl font-bold text-gray-900">
              {chartTitle}
            </h2>
            <select
              value={selectedPeriod}
              onChange={(e) => setSelectedPeriod(e.target.value as "week" | "month" | "year")}
              className="px-4 py-2 bg-gray-100/80 border border-gray-300 rounded-lg text-gray-800 focus:ring-2 focus:ring-teal-500 focus:border-teal-500 transition-all duration-300 hover:bg-gray-200"
            >
              <option value="week">Semaine</option>
              <option value="month">Mois</option>
              <option value="year">Année</option>
            </select>
          </div>
          <div className="relative h-[450px]">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart
                data={chartData}
                margin={{ top: 20, right: 30, left: 20, bottom: 20 }}
              >
                <CartesianGrid strokeDasharray="3 3" stroke="rgba(0, 0, 0, 0.05)" />
                <XAxis
                  dataKey={xAxisKey}
                  stroke="#374151"
                  tick={{ fontSize: 14, fontFamily: "Montserrat", fill: "#374151" }}
                />
                <YAxis
                  yAxisId="left"
                  stroke="#374151"
                  tick={{ fontSize: 14, fontFamily: "Montserrat", fill: "#374151" }}
                  tickFormatter={(value) => `${value / 1000}K DZD`}
                  label={{
                    value: "Ventes (DZD)",
                    angle: -90,
                    position: "insideLeft",
                    fontSize: 16,
                    fontFamily: "Montserrat",
                    fontWeight: "bold",
                    fill: "#374151",
                    offset: 15,
                  }}
                />
                <YAxis
                  yAxisId="right"
                  orientation="right"
                  stroke="#374151"
                  tick={{ fontSize: 14, fontFamily: "Montserrat", fill: "#374151" }}
                  tickFormatter={(value) => `${value}`}
                  label={{
                    value: "Articles vendus",
                    angle: 90,
                    position: "insideRight",
                    fontSize: 16,
                    fontFamily: "Montserrat",
                    fontWeight: "bold",
                    fill: "#374151",
                    offset: 15,
                  }}
                />
                <Tooltip
                  contentStyle={{
                    backgroundColor: "rgba(255, 255, 255, 0.95)",
                    borderRadius: "12px",
                    border: "1px solid rgba(0, 0, 0, 0.1)",
                    color: "#374151",
                    fontFamily: "Montserrat",
                    fontSize: "14px",
                    padding: "12px",
                    boxShadow: "0 4px 15px rgba(0, 0, 0, 0.1)",
                  }}
                  formatter={(value: number, name: string) =>
                    name === "sales" ? `${value.toLocaleString()} DZD` : `${value}`
                  }
                />
                <Legend
                  wrapperStyle={{
                    fontFamily: "Montserrat",
                    fontSize: "16px",
                    paddingTop: "20px",
                  }}
                />
                <Line
                  yAxisId="left"
                  type="monotone"
                  dataKey="sales"
                  stroke="#14B8A6"
                  strokeWidth={3}
                  dot={{ r: 5, fill: "#14B8A6", stroke: "#fff", strokeWidth: 2 }}
                  activeDot={{ r: 7, fill: "#14B8A6", stroke: "#fff", strokeWidth: 2 }}
                  name="Ventes (DZD)"
                  animationDuration={1500}
                />
                <Line
                  yAxisId="right"
                  type="monotone"
                  dataKey="itemsSold"
                  stroke="#EC4899"
                  strokeWidth={3}
                  dot={{ r: 5, fill: "#EC4899", stroke: "#fff", strokeWidth: 2 }}
                  activeDot={{ r: 7, fill: "#EC4899", stroke: "#fff", strokeWidth: 2 }}
                  name="Articles vendus"
                  animationDuration={1500}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      {/* Styles CSS */}
      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0); }
          50% { transform: translateY(-8px); }
        }
        .animate-float {
          animation: float 3s infinite ease-in-out;
        }
        .animate-pulse {
          animation: pulse 2s infinite;
        }
        @keyframes pulse {
          0%, 100% { opacity: 1; }
          50% { opacity: 0.7; }
        }
      `}</style>
    </div>
  );
}